/* ============================================================
  Placeholders for repeat styles.
  ============================================================ */

%menu {
  font-family: $headline;
  position: relative;

  li {
    display: block;

    @include respond-to('l') {
      display: inline-block;
    }
  }

  a {
    color: #fff;
    text-decoration: none;
    font-weight: 600;
    display: block;
    width: 100%;
    height: 100%;

    &:hover {
      text-decoration: underline;
    }
  }
}

%menu-item {
  padding: .75em;

  @include respond-to('xl') {
    padding: 1em;
  }
}

%no-underline-link {
  text-decoration: none;
  font-weight: 600;

  &:hover {
    text-decoration: underline;
  }
}

%button {
  color: #fff;
  background: $dark-blue;
  font-size: 1.25em;
  padding: .5em 2em;
  display: block;
  border: 2px solid transparent; // we need a 2px border to match the light version.
  
  // Default button hover is a blue bg gradient.
  &:hover {
    background: linear-gradient(44.37deg, $dark-blue 0%, $blue 100%);
  }

  // Add specific styles for light variant.
  &.light {
    color: $dark-blue;
    background: #fff;
    border: 2px solid $dark-blue;
  }
}

%nomargin,
.nomargin {
  margin: 0 !important;
}

%nopadding,
.nopadding {
  padding: 0 !important;
}

%nospacing,
.nospacing {
  margin: 0 !important;
  padding: 0 !important;
} 

%city-bg {
  position: relative;
  background: $blue; // fallback
  background: $blue-gradient-4;

  @include respond-to('l') {
    background: $dark-blue;
    background: $blue-gradient-3, url('../images/boston-skyline.jpg') no-repeat;
    background-size: cover;
    background-position: -120px 0;
  }

  @include respond-to('xl') {
    background-position: 0;
  }

  @include respond-to('xxl') {
    background-position: 0 -30px;
  }
}

%form-box {
  font-family: $body-copy;
  border: 2px solid $border-gray;
  border-radius: 3px;
  width: 100%;
  padding: 1.6em;
}

%centered {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

%dropdown {

  transform: translateX(100%);
  transition: transform .3s;

  &.active {
    transform: translateX(0);
  }

  @include respond-to('l') {
    transform: scaleY(0);
    transform-origin: top;
  
    &.active {
      transform: scaleY(1);
    }  
  }
}