/* ============================================================
  Styles for the header.
  ============================================================ */

header {
  @extend %city-bg;

  max-width: 1920px;
  margin: 0 auto;
  overflow: hidden;

  &.active {
    height: 780px;
  }

  @include respond-to('l') {
    min-height: 500px;
    overflow: visible;
  }

  .front & {
    padding: 0 0 7em;

    @include respond-to('l') {
      padding: 0;
    }
  }

  > div {

    @include respond-to('l') {
      padding: 2em 0;
    }

    @include respond-to('xl') {
      padding: 2em 5em;
    }

    @include respond-to('xxl') {
      padding: 2em 6.25em;
    }
  }
  
  &::after {
    content: '';
    width: 100%;
    position: absolute;
    bottom: 0;
    background: url('../images/header-wave-mobile.svg') no-repeat;
    height: 50px;

    @include respond-to('xxxs') {
      background: url('../images/header-wave-mobile-plus.svg') no-repeat;
    }

    @include respond-to('xxs') {
      background: url('../images/header-wave-tablet.svg') no-repeat;
    }

    @include respond-to('m') {
      background: url('../images/header-wave-tablet-landscape.svg') no-repeat;
    }

    @include respond-to('l') {
      background: url('../images/header-wave.svg') no-repeat;
    }

    @include respond-to('xxl') {
      background: url('../images/header-wave-xl.svg') no-repeat;
      height: 100px;
    }
  }

  // Logo image sizing.
  #block-paladin-branding {

    position: fixed;
    z-index: 500;
    background: $dark-blue;
    width: 100%;
    padding: .3em 0 .6em .3em;

    &.active {
      position: absolute;
      background: transparent;
    }

    @include respond-to('l') {
      position: relative;
      left: 1.25em;
      top: 0;
      width: 50%;
      z-index: 100;
      background: transparent;
      padding: 0;
    }

    @include respond-to('xl') {
      left: 0;
    }

    a {
      display: block;
      width: 200px;

      @include respond-to('s') {
        width: 280px;
      }
    }

    .slogan {
      display: none;

      @include respond-to('l') {
        margin: 1em 0 2em;
        display: block;

        span {
          color: #fff;
          margin: 0 1em 0 0;
          font-style: italic;
        }
      }
    }
  }
}
