/* ============================================================
  Colors
  ============================================================ */

$dark-blue: #123A7E;
$dark-blue-overlay: linear-gradient(rgba(18, 58, 126, .8),  rgba(18, 58, 126, .8)); // Needs to be a gradient so it can work as an overlay for bg images.
$darker-blue: #16305D;
$blue: #0275B7;
$menu-blue: rgba(2, 117, 183, .9);
$light-blue: #00A3CD;
$green: #0D772A;
$gray: #393939;
$light-gray: #F7F5F5;
$border-gray: rgba(57,57,57,0.25);
$menu-border-gray: rgba(255,255,255,0.43);
$white: #fff;
$red: #9c0000;
$teal: #0280A0;

// Gradients

$blue-gradient-1: linear-gradient(90deg, $dark-blue 0%, $blue 100%);
$blue-gradient-2: linear-gradient(90deg, $dark-blue 0%, $blue 46.83%, $light-blue 100%);
$blue-gradient-3: linear-gradient(180deg, rgba(22,48,93,0.9) 0%, rgba(18,58,126,0.55) 30.97%, rgba(255,255,255,0) 100%);
$blue-gradient-4: linear-gradient(180deg, $dark-blue 30%, $blue 100%);