/* ============================================================
  Styles for the footer.
  ============================================================ */
footer {
  display: flex;
  flex-direction: column;
  color: #fff;
  max-width: 1920px;
  margin: 0 auto;

  a {
    color: #fff;
  }

  .footer-top {
    background: $dark-blue;
    background: $dark-blue-overlay, url('../images/footer.jpg') no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    padding: 6em 1.5em 1.5em;

    &::before {
      content: '';
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      // background: url('../images/footer-wave-mobile.svg') no-repeat; // will be replaced soon
      height: 100px;

      @include respond-to('xxs') {
        background: url('../images/footer-wave-tablet.svg') no-repeat;
      }
  
      @include respond-to('m') {
        background: url('../images/footer-wave-tablet-landscape.svg') no-repeat;
      }
  
      @include respond-to('l') {
        background: url('../images/footer-wave.svg') no-repeat;
      }
  
      @include respond-to('xxl') {
        background: url('../images/footer-wave-xl.svg') no-repeat;
      }
    }
  
    @include respond-to('s') {
      padding: 6em 5em 3em;
    }

    @include respond-to('l') {
      flex-direction: row;
      padding: 7em 5em;
    }
  
    h4 {
      @extend %nospacing;
      font-size: 1.2em;
    }

    p {
      margin: .15em 0 1em;
    }

    #block-paladin-footer {
      margin: 2em 0 0;
      line-height: 1.75;

      a {
        @extend %no-underline-link;
      }
    }

    #block-footermiddlemenu {
      a {
        @extend %no-underline-link;
      }

      .menu-item--expanded {
        margin: 0 0 1.5em;

        > a {
          border-bottom: 2px solid $light-blue;
          font-weight: 600;
          display: block;
          margin: 0 0 .75em;
        }
      }
    }

    .block-social-media-links {
      li {
        padding: 0 1em 0 0;
      }
    }

    #block-twitterblock {
      margin: 2em 0 0;

      h2 {
        margin: .5em 0;
        font-size: 1.3em;
      }
    }
  }

  .footer-bottom {
    background: $gray;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5em 1.5em;
    font-weight: 600;
    flex-direction: column;
    text-align: center;

    @include respond-to('s') {
      padding: .5em 5em;
    }

    @include respond-to ('m') {
      flex-direction: row;
      text-align: left;
    }

    a {
      display: flex;
      align-items: center;
      text-decoration: none;
      font-weight: 600;
      margin: .5em 0;

      @include respond-to('m') {
        margin: 0;
      }

      img {
        display: inline-block;
        width: 30px;
        height: 30px;
        margin: 0 0 0 .75em;
      }  
    }
  }
}