/* ============================================================
  Styles for the main menu.
  ============================================================ */

  #block-main-menu {
    @extend %menu;
    
    font-size: 1.1em;
    width: 100%;
    background: $blue;
    top: 0;
    padding: 4em 1em 10em;
    opacity: 0;
    height: 0;
    transition: .3s opacity;
    z-index: -1;
  
    &.active {
      opacity: 1;
      height: auto;
      z-index: 300;
    }

    @include respond-to('l') {
      background: $menu-blue;
      top: auto;
      padding: 0;
      z-index: 500;
      display: block;
      opacity: 1;
      height: auto;
    }

    a {
      font-weight: 600;
      font-size: 1em;
      position: relative;

      @include respond-to('l') {
        font-size: .9em;
      }

      @include respond-to('xl') {
        font-size: .95em;
      }

      @include respond-to('xxl') {
        font-size: 1em;
      }
    }

    .menu-item--expanded {

      @include respond-to('l') {
        position: relative;
      }

      &.active {
        background: $light-gray;

        a {
          color: $dark-blue;
        }  
      }
    }

    .menu-level-0 {
      display: inline-block;
      border-top: 2px solid $menu-border-gray;
      border-bottom: 2px solid $menu-border-gray;
      width: 100%;
      padding: .5em 0;

      > li {
        @extend %menu-item;
      }

      @include respond-to('l') {
        border: 0;
        width: auto;
        display: flex;
        justify-content: space-between;
        padding: 0 .5em;
      }

      @include respond-to('xl') {
        padding: 0;
      }

      .arrow {
        display: block;
        position: absolute;
        font-size: 1.75em;
        right: 0;
        bottom: -.15em;

        @include respond-to('l') {
          display: none;
        }
      }
    }

    .menu-level-1 {
      @extend %dropdown;
      background: $light-gray;
      position: absolute;
      left: 50%;
      right: 50%;
      top: 13em;
      padding: 1em;
      width: 100vw;
      height: 100%;
      margin-left: -50vw;
      margin-right: -50vw;
      z-index: 500;

      @include respond-to('xxs') {
        top: 14em;
      }

      @include respond-to('l') {
        width: 300px;
        box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.5);
        top: 2.6em;
        left: 0;
        right: 0;
        margin: 0 !important;
        position: absolute;
        height: auto;
      }
      @include respond-to('xl') {
        top: 3.2em;
      }

      li {
        display: block;
        padding: .5em 0;
        position: relative;

        &:first-of-type {
          display: inline-block;
          border-bottom: 2px solid $light-blue;
          margin: 0 0 .75em;

          @include respond-to('l') {
            display: block;
            border: 0;
            margin: 0;
          }
        }
      }

      a {
        color: $dark-blue;
        padding: 0 0 0 2em;
        display: block;
        position: relative;
        z-index: 100;
      }

      .arrow {
        display: none;
      }

      svg {
        position: absolute;
        color: $light-blue;
        top: .65em;
        left: 0;

        @include respond-to('l') {
          z-index: -1;
        }
      }
    }
    .mobile {
      display: block;

      .search-box {
        position: relative;
        bottom: auto;
        display: flex;
        z-index: 500;

        .search-button {
          bottom: .35em;
          right: .4em;
        }
      }
  
      .menu-phone {
        margin: 1em 0;
      }

      .back {
        padding: 1em;
        margin: 0 0 0 1em;

        svg {
          font-size: 2em;
          color: $dark-blue;
          top: .3em;
          left: -.4em;
        }
      }
  
      @include respond-to('l') {
        display: none;
      }
    }
  }

  .mobile-menu-trigger {
    position: fixed;
    top: .5em;
    right: .5em;
    color: #fff;
    z-index: 500;

    &.active {
      position: absolute;
    }

    @include respond-to('l') {
      display: none;
    }

    .burger,
    .close-x {
      font-size: 2em;
      display: block;
      width: 50px;
    }

    .close {
      display: none;
    }

    .txt {
      margin: .5em 0 0;
    }

    &.active {
      .burger,
      .txt {
        display: none;
      }
      .close {
        display: block;
      }
    }
  }
